@media screen and (max-width: 1550px) and (min-width: 1001px) { 



    .navbar {
        width: 100%;
        height: 10%;
        background-color: #2e1e12;
        display: flex;
        justify-content: space-between;  
    }
    
    .space_filler_left {
        width: 100%;
        height: 100%;
    }
    
    .navbar_menu {
        width: 900px;
        height: 100%;
        display: flex;
        justify-content: space-evenly;    
    }
    
    .navbar_menu_title {
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: 'Luckiest Guy', cursive;
        font-size: 25px;
        color: white;
    }
    .navbar_menu_title>p{
        transform: translateY(4px);
    }
    
    
    #navbar_title_quiz {
        width: 100px;
        background-color: #e8c242;
    }
    #navbar_menu_title_potions {width: 150px; position: relative; display: flex; justify-content: center; align-items: center;}
    #navbar_menu_title_craft {width: 110px; position: relative; display: flex; justify-content: center; align-items: center;}
    #navbar_menu_title_buysell {width: 155px; position: relative; display: flex; justify-content: center; align-items: center;}
    #navbar_menu_title_leaderboard {width: 220px; position: relative; display: flex; justify-content: center; align-items: center;}
    
    
    #power_blocker_potion {
        height: 70%;
        position: absolute;
        display: none;
    }
    #power_blocker_craft {
        height: 70%;
        position: absolute;
        display: none;
    }
    #power_blocker_buysell {
        height: 70%;
        position: absolute;
        display: none;
    }
    #power_blocker_leaderboard {
        height: 70%;
        position: absolute;
        display: none;
    }
    
    .link {
        text-decoration: none;
    }
    
    .player_money {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        justify-self: flex-end;
        gap: 10px;
    
    }
    
    .player_money>img {
        height: 40px;
        margin-right: 20px;
    }
    
    .player_money>p {
        font-size: 25px;
        font-family: 'Luckiest Guy', cursive;
        color: white;
        margin: 0;
        transform: translateY(4px);
    }






}



/* My small computer 1366x647 */ 
@media screen and (max-width: 1550px) and (min-width: 1001px) and (max-height: 700px) and (min-height: 501px) { 
    .navbar_menu_title>p{transform: translateY(0px);}
    .player_money>p {font-size: 25px; font-family: 'Luckiest Guy', cursive; color: white; margin: 0; transform: translateY(0px);}
    .player_money>img {height: 35px; margin-right: 20px; transform: translateY(3px);}
}

/* Ubunto 1292x668 */
@media screen and (max-width: 1300px) and (min-width: 1280px) and (max-height: 670px) and (min-height: 660px) {
    .navbar_menu_title>p{transform: translateY(4px);}
}